import React, { Component } from "react";
import logo from '../images/Chatter_Logo_Transparent.png';
import '../css/chat.css';
import { Link, Redirect } from 'react-router-dom';
import io from 'socket.io-client';
import Message from '../components/Message';
import axios from "axios";

class Home extends Component {
    constructor() {
        super();

        this.state = {
            messages: [],
            messageInput: '',
        }

        this.handleChange = this.handleChange.bind(this);
        this.componentDidMount = this.componentDidMount.bind(this);
        this.addMessage = this.addMessage.bind(this);
        this.handleKeydown = this.handleKeydown.bind(this);
        this.logOut = this.logOut.bind(this);
    };

    componentDidMount() {
        this.socket = io(window.location.host);
        document.addEventListener("keydown", this.handleKeydown, false);
        this.socket.on("chat-message", this.addMessage);
    }

    handleKeydown(event) {
        if (event.keyCode === 13) {
            this.socket.emit("chat-message", { msg: this.state.messageInput });
            this.setState({ messageInput: '' });
        }
    }

    addMessage(message) {
        this.setState({ messages: [...this.state.messages, { id: message.id, msg: message.msg }] })
    }

    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value,
        });
    }

    componentWillUnmount() {
        document.removeEventListener("keydown", this.handleKeydown, false);
    }

    logOut() {
        console.log('logout running')
        axios.get('/logout').then(response => {
            this.props.updateAppState(response.data);
        });
    }

    render() {
        if (!this.props.loggedIn) { return <Redirect to='/login'></Redirect> }
        
        return (
            <div id='chatWindow'>
                <div className='row p-1'>
                    <div className='col'>
                        <img alt='Chatter logo' src={logo} ></img>
                    </div>
                    <div className='col text-center'><button type='button' className='btn btn-secondary' onClick={this.logOut}>Log Out</button></div>
                </div>
                <div className='row'>
                    <input type='text' className='form-control' name='messageInput' value={this.state.messageInput} onChange={this.handleChange} ></input>
                </div>
                <div className='row p-2'>
                    <div className='col' id="messages">
                        {this.state.messages.map(n => (
                            <Message key={n.id} id={n.id} msg={n.msg} />
                        ))}
                    </div>
                </div>
            </div>
        )
    }
}

export default Home;



